.team_wrap {
  flex-wrap: wrap;
  justify-content: center;
  gap: 25px;
  width: 100%;
  height: 100%;
  display: flex;
}

.team_card {
  perspective: 1000px;
  width: 250px;
  height: 300px;
  position: relative;
}

.team_front, .team_back {
  backface-visibility: hidden;
  border: 2px solid var(--color-blue-component);
  border-radius: 10px;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  transition: all 1s;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
}

.team_back {
  background-color: #bdbdbd;
  transform: rotateY(180deg);
}

.team_card:hover .team_front {
  transform: rotateY(180deg);
}

.team_card:hover .team_back {
  transform: rotateY(360deg);
}

.team_people {
  flex-direction: column;
  align-items: center;
  gap: 5px;
  display: flex;
}

.team_name {
  letter-spacing: .04em;
  color: var(----dark-theme-componemt);
  font-size: 25px;
  line-height: 1.1875;
}

.team_position {
  letter-spacing: .04em;
  color: rgba(17, 17, 17, .6);
  font-size: 20px;
  line-height: 1.125;
}

.team_social-list {
  justify-content: center;
  gap: 5px;
  margin-top: 15px;
  display: flex;
}

.team_social-list-link {
  background-color: var(--primary-brand);
  transition: background-color .5s var(--timing-function);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 40px;
  display: flex;
}

.team_social-list-link:hover, .team_social-list-link:focus {
  background-color: var(--pressed-state);
}

